

.hero-container {
    color: white;
    height: 100vh;
    width: 100%;
    padding-top:200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    object-fit: contain;
}

.hero-text{
    margin: 200px;
    
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    width: 100%;
    display: flex;
    font-size: 2.5rem;
    flex-direction: column;
    align-items: center;
    object-fit: contain;
}

.hero-text >h1{
    margin-bottom: 5%;
}

.hero-btns > button {
    margin: 100px;
}

@media screen and (max-width: 960px) {
    .hero-text{
        margin: 100px;
        box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
        width: 100%;
        display: flex;
        font-size: 1.3rem;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        object-fit: contain;
    }
    .hero-btns > button {
        justify-content: center;
        margin: 5px;
    }
    .hero-text >h1{
        margin-bottom: 2rem;
    }
    .hero-text >p {
        margin-bottom: 30px;
    }
}
