  padding: 0;
  font-family: 'Montserrat', sans-serif;
}
video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed ;
  display: block;
  z-index: -1;
  background-attachment: fixed;
}
.pagess{
  object-fit: contain;
  position:relative;
}