.TecContainer {
    display: flex;
    text-align: center;
    width: 100%;
    height: 100vh;
    padding-top: 80px;
    color: white;
    justify-content:center;
}

.TecText{
    justify-content:center;
    text-align: center;
    margin:10px;
    padding-top: 10%;
    float: right;
    width: 50%;
}

.TecText1, .TecText2, .TecText3{
        width: 100%;
        height:auto;
        border-radius: 15px;
        flex: 0 1 40%;
        overflow: hidden;
        padding: 10px;
        display: flex;
        position: relative;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: rgba(0,0,0,0.5);
        backdrop-filter: blur(15px);
        border: 1px solid rgba(255,255,255,.2);
        -webkit-box-shadow: 0px 10px 33px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 10px 33px 0px rgba(0,0,0,0.75); 
        box-shadow:0px 10px 33px 0px rgba(0,0,0,0.75); 

}
.TecImage{
    width: 50%;
    height: 100%;
    float: left;
    text-align:center;
    margin-top:220px;
    
    
    
}

.TecImage >img {
    
    width: 80%;
    height: auto;
    
}
.TecText1 >p {
    text-align: center;
    display: list-item  ;   
    padding-top: 1%;       
    list-style-type: disc;       
    list-style-position: inside;
}
.TecText2 >p {
    text-align: center;
    display: list-item;       
    padding-top: 1%;          
    list-style-type: disc;       
    list-style-position: inside;
}

.TecText3 >p {
    text-align: center;
    display: list-item;          
    padding-top: 1%;       
    list-style-type: disc;       
    list-style-position: inside;
}

@media screen and (max-width: 960px) {
    .TecImage{
        width: 100%;
        padding-top: 5%;
        float:none;
        text-align:center;   
    }
    .TecContainer {
        display: grid;
        text-align: center;
        width: 100%;
        height: 100%;
        padding-top: 80px;
        color: white;
        justify-content:center;
        font-size: small;

    }
    .TecImage >img {
        width: 50%;
        padding-top: 5%;
        height: auto;
    }
    .TecText1, .TecText2, .TecText3{   
        width: 100%;
        justify-content:center;
        text-align: center;
        height:auto;
        border-radius: 15px;
        padding-left: 0%;
        padding: 0px;
        display:relative;
    }
    .TecText{
        justify-content:center;
        text-align: center;
        padding-bottom: 25%;
        width: 100%;
        margin:0px;
        float:none;
    }
}

