.contactPage{
    width: 100%;
    height: 100vh;
    padding-top: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
}
.h1-contact{
    text-align: center;
    color: white;
    font-size: 3rem;
    text-shadow: 0px 1px 3px rgba(0,0,0,0.6);
}
#contact-container{
    width: 80%;
    min-width: 800px;
    height: auto;
    border-radius: 15px;
    overflow: hidden;
    padding: 10px;
    display: flex;
    background: rgba(0,0,0,0.5);
    backdrop-filter: blur(15px);
    border: 1px solid rgba(255,255,255,.2);
    -webkit-box-shadow: 0px 10px 33px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 33px 0px rgba(0,0,0,0.75); 
    box-shadow:0px 10px 33px 0px rgba(0,0,0,0.75); 

    .contact-info{
        width: 100%;
        min-width: 51.5%;
        background-color: #5904c2;
        border-radius: 15px;
        flex: 0 1 40%;
        padding: 40px;
        color: white;
        display: flex;
        flex-direction: column;
        .h4-contact{
            font-size: 2rem;
            font-weight: 500;
            //margin-top: 0;
            margin: 0;
        }
        .p-contact{
            color: rgba(255,255,255,.7);
            font-size: 1.2rem;
            font-weight: 300;
            margin-bottom: 2rem;
        }
        .icon-text{
            display: inline-flex;
            justify-content: flex-start;
            align-items: center;
            .fas{
                flex: 0 0 20px;
                font-size: 1.8rem;
                margin-right: 2rem;
                color: #3acbf7;;
            }
            span{
                padding: 5%;
                font-size: 2vh;
                font-weight: 300;
            }
        }
        .social-media{
            display:flex;
            width:50%;
            justify-content: space-between;
            
            .icon-circle{
                flex: 0 1 40px;
                display: flex;
                justify-content: center;
                align-items: center;
                height: 40px;
                border-radius: 50%;
                transition: .3 all ease-in-out;
                &:hover{
                    background: #3acbf7;
                }
                .fab{
                    font-size: 25px;
                    color: white;
                }
            }
        }
    }
    .form-contact{
        padding: 40px;
        width: 100%;
        .col{
            display: flex;
            width: 100%;
            flex: 0 0 100%;
        }
        .form-group{
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
            flex: 0 1 100%;
            padding-right: 40px;
            .label-contact{
                font-size: 0.9rem;
                color: white;
                margin-bottom: 1rem;
            }
            input[type="text"],input[type="tel"],input[type="email"], textarea{
                width: 100%;
                border: none;
                cursor:pointer;
                padding:10px;
                font-size: 1.5rem;
                font-weight: 300;
                color: white;
                background: transparent;
                margin-bottom: 2rem;
                border-bottom: 2px solid rgba(255,255,255,.4);
                &:focus{
                    border:none;
                    outline: 0 none;
                    border-bottom: 1px solid #05f7ff;
                }
            }
            &:nth-child(2){
                padding-right: 0;
            }
            .radio-buttons{
                display: flex;
                justify-content: flex-start;
                margin-bottom: 2rem;
                .radio-button{
                    margin-right: 20px;
                }
                .label-contact{
                    font-size: 1rem;
                }
            }
            &.solo{
                padding: 0;
            }
        }
        #btn-mess{
            display: flex;
            justify-content: space-between;
            
            .confirmation-message{
                justify-self: flex-start;
                color: white;
                padding: 8px 20px;
                border-radius: 10px;
                font-size: 20px;
                text-align: center;
                background: green;
                visibility: hidden;
            
            }
            .confirmation-messageAppear{
                justify-self: flex-start;
                color: white;
                padding: 8px 20px;
                border-radius: 10px;
                font-size: 20px;
                text-align: center;
                background: green;
                display: flex;
            
            }
        }
    }

}
@media (max-width: 960px) {
    .contactPage{
        font-size: small;
        height: 100%;
    #contact-container{
        font-size: small;
        display: block;
        width: 95%;
        height: 100%;
        min-width: 0px;
        .form-contact{
            display: block;
            min-width: none;
            .col{
                display: block;
                min-width: none;
            }
            .form-group{
                padding-right: 0px;
            }
            .label-contact{
                font-size: 0.9rem;
                color: white;
                margin-bottom: 0.2rem;
            }
        }
        .contact-info{
            width: 100%;
            background-color: #5904c2;
            border-radius: 15px;
            flex: 0 0 40%;
            padding: 40px;
            color: white;
            display: block;
            .h4-contact{
                font-size: 1.5rem;
                font-weight: 300;
                //margin-top: 0;
                margin: 0;
            }
            .p-contact{
                color: rgba(255,255,255,.7);
                font-size: 1rem;
                font-weight: 300;
                margin-bottom: 2rem;
            }
            .icon-text{
                display: flex;
                justify-content: space-between;
                align-items: center;
                .fas{
                    flex: 0 0 20px;
                    font-size: 1.8rem;
                    margin-right: 2rem;
                    color: #3acbf7;;
                }
                span{
                    padding: 3%;
                    font-size: 0.8rem;
                    font-weight: 300;
                    align-items: center;
                }
        }

    }
}
} }   
